


.header {
    min-height: 100vh;
    background-color:#272727;
}

.skillsContainer {
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;

}


.skills {


    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

@media screen and (max-width: 642px){
    .skills{
        justify-content: center;
    }
}